body {
  background-color: #fff;
  background-image: radial-gradient(#ccc .5px, rgba(0, 0, 0, 0) .5px);
  background-size: 20px 20px;
  margin: 40px;
  font-family: Arial, sans-serif;
}

.country-card {
  max-width: 800px;
  background-color: #fff;
  border-radius: 12px;
  margin: 0 auto;
  padding: 2rem;
}

.country-card h2 {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 36px;
  font-weight: 600;
}

.country-content {
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.country-text {
  max-width: 50%;
  flex: 1;
  font-size: 18px;
}

.country-text .label {
  font-size: 20px;
  font-weight: bold;
}

.country-text ul {
  margin-top: .5rem;
  padding-left: 20px;
}

.country-text ul li {
  margin-bottom: 5px;
  list-style: disc;
}

.country-content img {
  width: 250px;
  height: auto;
  object-fit: contain;
  margin-left: 40px;
}

